body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-xs: 12px;

  /* Colors */
  --color-snow: #fffcfc;
  --color-white: #fff;
  --color-gainsboro: #d9d9d9;
  --color-black: #000;
  --color-darkgray: #969696;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-xs: 12px;
  --padding-12xs: 1px;
  --padding-9xs: 4px;
  --padding-sm: 14px;
}
